<template>
<div class="form-overflow">
    <div class="fo-1">
        <ul>
            <li>
                <span class="number">
                    <a href="" @click="goToFormData()">{{ formDataLength }}</a>
                </span>
                <span class="info-name">表单总数据 </span>
            </li>
            <li>
                <span class="number">
                    <a href="">{{ formDataCountDaily }}</a>
                </span>
                <span class="info-name">今日新增 </span>
            </li>
            <li>
                <span class="number">
                    <a href="">1</a>
                </span>
                <span class="info-name">表单被浏览 </span>
            </li>
            <li>
                <div>
                    <img :src="this.formQRCode" alt="" height="175px" />
                </div>
            </li>
        </ul>
    </div>

    <div class="fo-2">
        <ul>
            <!-- 左边那块 -->
            <li>
                <div class="setting-row">
                    <div class="setting-row-left">表单名称</div>
                    <div class="setting-row-right">{{ formTitle }}</div>
                </div>
                <div class="setting-row">
                    <div class="setting-row-left">表单创建时间</div>
                    <div class="setting-row-right">{{ formCreateTime }}</div>
                </div>
                <div class="setting-row">
                    <div class="setting-row-left">表单描述</div>
                    <div class="setting-row-right">{{ formDesc }}</div>
                </div>
                <div class="setting-row">
                    <div class="setting-row-left">表单类型</div>
                    <div class="setting-row-right">{{ formType }}</div>
                </div>
            </li>
            <!-- 右边那块 -->
            <li>
                <div class="setting-row">
                    <div class="setting-row-left">表单ID</div>
                    <div class="setting-row-right">{{ formId }}</div>
                </div>
                <div class="setting-row">
                    <div class="setting-row-left">填写者权限</div>
                    <div class="setting-row-right">所有人可填</div>
                </div>
                <div class="setting-row">
                    <div class="setting-row-left">表单开启/停止</div>
                    <div class="setting-row-right">
                        <el-button size="mini" round @click="tanchuang">发布地址</el-button>
                        <!-- <el-button type="text">文字按钮</el-button> -->
                    </div>
                </div>
                <div class="setting-row">
                    <div class="setting-row-left">表单是否发布</div>
                    <div class="setting-row-right">
                        <div>
                            <el-switch style="display: block" @change="fabu" v-model="openValue" active-color="#13ce66" inactive-color="#ff4949" active-text="已发布" inactive-text="未发布">
                            </el-switch>
                        </div>
                    </div>
                </div>
            </li>
        </ul>
    </div>

    <div class="fo-3">
        <formtable :tableData="this.tableData" :tableColumn="this.tableColumn"></formtable>
    </div>

    <div></div>
</div>
</template>

<script>
import formtable from "@/components/EditPageComp/chartcomp/formtable.vue";
import {
    getOneTable,
    gettableData,
    getDataCountDaily,
    gettableColumn,
} from "@/network/formdata.js";

import {
    formOpen
} from "@/network/form.js";
export default {
    components: {
        formtable,
    },
    data() {
        return {
            openValue: "",
            tableColumn: [],
            tableData: [],
            formId: "",
            formCreateTime: "",
            formTitle: "",
            formDesc: "",
            formType: "",
            formDataLength: "",
            formDataCountDaily: "",
            formQRCode: "",
        };
    },
    methods: {
        tanchuang() {
            getOneTable(this.formId).then((res) => {
                this.$prompt(
                        "<p>将问卷地址粘贴到微信公众号，或者直接发给填表者！</p><input class='message-box-release__input' type='text' readonly value=" +
                        "http://localhost:8080" +
                        res.data.formUrl +
                        ">",
                        "发布地址", {
                            customClass: "message-box-release",
                            dangerouslyUseHTMLString: true,
                            confirmButtonText: "直接打开",
                            // cancelButtonText: "取消发布",
                            cancelButtonClass: "message-box-release__copy",
                            distinguishCancelAndClose: true,
                            showInput: false,
                        }
                    )
                    .then(() => {
                        this.$router.push(res.data.formUrl);
                    })
                    .catch((action) => {
                        if (action === "cancel") {
                          console.log("关闭");
                        }
                    });
            });
        },
        fabu() {
            formOpen(this.formId).then(() => {
                console.log("已切换状态");
            });

            console.log(this.openValue);
        },
        gettableDatas() {
            this.axios
                .all([gettableData(this.formId), gettableColumn(this.formId)])
                .then(
                    this.axios.spread((res1, res2) => {
                        var allData = res1.data;
                        var allQuestion = res2.data;
                        var ans = [];
                        var arrData = [];

                        //将返回的标题头 id 与 标题文字 拼接成键值对，存入数组 prop和label，以符合element ui 的格式
                        for (let questionIndex in allQuestion) {
                            // console.log(res.data[questionIndex].templateContent);
                            var paramOption = {};
                            paramOption.prop = allQuestion[questionIndex].formTemplateId + "";
                            paramOption.label = allQuestion[questionIndex].templateTitle;
                            this.tableColumn.push(paramOption);
                        }

                        //答案与id拼接
                        for (let i in allData) {
                            //循环表身单行 拿 表身的formTemplateId
                            for (let j in allQuestion) {
                                //循环表头 拿 表头的formTemplateId，
                                let paramId = {};
                                let paramData = {};
                                paramId["dataId"] = allData[i].dataId;
                                // arrAns.push(paramData)
                                for (let k in allData[i].dataDetailsList) {
                                    //判断表身formTemplateId和表头formTemplateId是否相等
                                    if (
                                        allQuestion[k].formTemplateId ==
                                        allData[i].dataDetailsList[j].formTemplateId
                                    ) {
                                        //如果相等，就拿表头中的 问题类型templateName 开始判断
                                        //----------------------------------1、如果为单选 则处理object
                                        if (
                                            allQuestion[k].templateName == "radio" ||
                                            allQuestion[k].templateName == "select" ||
                                            allQuestion[k].templateName == "picradio"
                                        ) {
                                            //如果不为空值或者空数组，做处理
                                            if (
                                                allData[i].dataDetailsList[j].dataContent != "[]" &&
                                                allData[i].dataDetailsList[j].dataContent != ""
                                            ) {
                                                ans = JSON.parse(
                                                    allData[i].dataDetailsList[j].dataContent
                                                );
                                                ans = ans.value;
                                            }
                                            //空值则赋一个“”
                                            else {
                                                ans = JSON.parse('{"key":0,"value":""}');
                                                ans = ans.value;
                                            }
                                            // console.log(ans);
                                        }
                                        //----------------------------------2、如果为多选 则处理array
                                        else if (
                                            allQuestion[k].templateName == "checkbox" ||
                                            allQuestion[k].templateName == "piccheckbox"
                                        ) {
                                            //如果为不为空值或者空数组，做处理
                                            if (
                                                allData[i].dataDetailsList[j].dataContent != "[]" &&
                                                allData[i].dataDetailsList[j].dataContent != ""
                                            ) {
                                                // 将数组拆开拿每一个值存入对应位置
                                                let result = eval(
                                                    allData[i].dataDetailsList[j].dataContent
                                                );
                                                ans = result.map((option) => option.value + " ");
                                            }
                                            //空值则赋一个“”
                                            else {
                                                ans = JSON.parse('{"key":0,"value":""}');
                                                ans = ans.value;
                                            }
                                        } else {
                                            ans = allData[i].dataDetailsList[j].dataContent;
                                        }
                                    }
                                }
                                paramData[allData[i].dataDetailsList[j].formTemplateId] = ans;
                                // console.log(paramData);
                                arrData.push(paramId);
                                arrData.push(paramData);
                            }
                            let result = Object.assign(...arrData);
                            // console.log(result);
                            arrData = [];
                            this.tableData.push(result);
                        }
                    })
                );
        },
        goToFormData() {
            this.$router.push({
                path: "/formFill/formdata/" + this.formId,
            });
        },
        getOneTables() {
            //查看当天填写人数
            getDataCountDaily(this.formId).then((res) => {
                this.formDataCountDaily = res.data;
            });
            getOneTable(this.formId).then((res) => {
                //当前表单所有数据
                console.log("当前表单所有数据");
                console.log(res.data); //formId全部数据
                this.formCreateTime = res.data.formCreateTime;
                this.formTitle = res.data.formTitle;
                this.formDesc = res.data.formDesc;
                this.formType = res.data.formType;
                this.formQRCode = this.$store.state.getURL + res.data.formQRCode;
                // console.log(res.data.formOpen);
                if (res.data.formOpen == 0) {
                    this.openValue = false;
                } else {
                    this.openValue = true;
                }
                console.log(this.openValue);
            });
        },
        getAllData() {
            gettableData(this.formId).then((res) => {
                console.log(res.data);
                this.formDataLength = res.data.length;
            });
        },
    },
    created() {
        sessionStorage.setItem("TagActiveName", "second");
        //获取formId
        this.formId = this.$route.params.id;
        this.getOneTables();
        this.getAllData();
        this.gettableDatas();
    },
};
</script>

<style>
.form-overflow {
    margin: 21px auto 0;
    width: 1200px;
}

.message-box-release__input {
    padding: 10px;
    width: 100%;
    height: 40px;
    line-height: 40px;
    border-radius: 4px;
    border: 1px solid rgb(102, 177, 255);
    font-size: 24px;
    cursor: no-drop;
    outline: none;
}

/* -------------fo-1------------------- */
.fo-1>ul {
    /*ul顶满fo */
    margin-top: 20px;
    margin-bottom: 20px;
    display: flex;
}

/* li之间距离 */
.fo-1>ul>li {
    height: 180px !important;
    margin-right: 20px;
}

.info-name {
    font-size: 16px;
}

.fo-1>ul>li:last-child {
    margin-right: 0px;
}

.fo-1>ul>li {
    flex: 1;
    /* li顶满ul */
    height: 100px;
    border: 1px solid #ebebeb;
    background: #ffffff;
}

.number {
    width: 100%;
    font-size: 40px;
    margin-top: 50px;
    margin-bottom: 5px;
    display: block;
}

.number a {
    color: hsl(216, 81%, 53%);
}

/* -------------fo-2------------------- */
.fo-2>ul {
    /*ul顶满fo */
    display: flex;
    margin-top: 30px;
    margin-bottom: 20px;
    /* background: chartreuse; */
}

/* li之间距离 */
.fo-2>ul>li {
    margin-right: 20px;
}

.fo-2>ul>li:last-child {
    margin-right: 0px;
}

.fo-2>ul>li {
    flex: 1;
    /* li顶满ul */
    height: 130px;
    border: 1px solid #ebebeb;
    background: #ffffff;
}

.setting-row {
    margin: 10px;
    height: 20px;
    /* background: chartreuse; */
}

.setting-row-left {
    float: left !important;
}

.setting-row-right {
    float: right !important;
    color: hsl(216, 81%, 53%);
}

/* -------------fo-3------------------- */
.fo-3 {
    margin-top: 30px;
    margin-bottom: 20px;
    /* padding-top: 100px; */
    height: 300px;
    overflow: auto;
}
</style>
